import React from 'react';
import './Header.css';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Hidden from '@material-ui/core/Hidden';
import withWidth from '@material-ui/core/withWidth';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import MenuOutlinedIcon from '@material-ui/icons/MenuOutlined';
import ContactsOutlinedIcon from '@material-ui/icons/ContactsOutlined';
import HelpOutlineOutlinedIcon from '@material-ui/icons/HelpOutlineOutlined';
import { Link } from 'react-router-dom';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import Linking from './Link'
function Header(props) {
 const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
     <Linking />
    </div>
  );
  const useStyles = makeStyles((theme) => ({
        root: {
          flexGrow: 1,
        },
        container: {
          display: 'flex',
          flexWrap: 'wrap',
        },
        paper: {
          padding: theme.spacing(2),
          textAlign: 'center',
          color: theme.palette.text.secondary,
          flex: '1 0 auto',
          margin: theme.spacing(1),
        },
      }));
      const classes = useStyles();
      const { width } = props;

      return (
        <div className="header">
          <Link to="/">
            <div className="header_logo">
<img src="https://www.flyploader.live/media/files/google.svg" alt="logo"/>
                </div>
</Link>
                <Hidden only={['xs']}>
                <div className="header_link">
                   <Link to="/aboutus">
                    <div className="anchor_icon">
                        <InfoOutlinedIcon/>
                <p className="anchor">About us</p>
                </div>
                </Link>
                <Link to="/contactus">
                <div className="anchor_icon">
                <ContactsOutlinedIcon/>
                <p className="anchor">Contact us</p>
                </div>
                </Link>
                <Link to="/we_solving">
                <div className="anchor_icon">
                <HelpOutlineOutlinedIcon/>
                <p className="anchor">Solving</p>
                </div>
                </Link>
                 </div>
        </Hidden>
    
        <Hidden only={['lg', 'md', 'sm', 'xl', ]}>
                {['right'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}>
            <MenuOutlinedIcon style={{color:'white',cursor:'pointer',}}/>
</Button>
          <SwipeableDrawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
        
    
         </Hidden>
        
        
            
        </div>
    );
}
Header.propTypes = {
    width: PropTypes.oneOf(['lg', 'md', 'sm', 'xl', 'xs']).isRequired,
  };
  
export default withWidth()(Header);