import React from 'react';
import './Call.css'
import PhoneIcon from '@material-ui/icons/Phone';
function Call() {
    return (
        <div className="call">
            <div className="call_box">
          <div className="call_svg">
              <img src="https://www.flyploader.live/media/files/undraw_phone_call_grmk.svg" alt="image"/>
          </div>
          <div className="call_title">
    <h2>Call us To Place an order</h2>
          </div>
          <div className="call_text">
    <p>Company No:- 999999999</p>
          </div>
          <div className="call_icon">
          <PhoneIcon/>
          </div>
          </div>
        </div>
    );
}

export default Call;