import React from 'react';
import './About.css';
import PropTypes from 'prop-types';
import Hidden from '@material-ui/core/Hidden';
import { makeStyles } from '@material-ui/core/styles';
import withWidth from '@material-ui/core/withWidth';
function About({image, title, text, head, size},props) {
    const useStyles = makeStyles((theme) => ({
        root: {
          flexGrow: 1,
        },
        container: {
          display: 'flex',
          flexWrap: 'wrap',
        },
        paper: {
          padding: theme.spacing(2),
          textAlign: 'center',
          color: theme.palette.text.secondary,
          flex: '1 0 auto',
          margin: theme.spacing(1),
        },
      }));
      const classes = useStyles();
       const { width } = props;
    return (
        <div className="about">
            <Hidden only={['lg','xl']}>
            <div className="about_header">
    <h1>{title}</h1>
            </div>
            <div className="about_info" style={{display:'grid',placeItems:'center'}}>
            <div className="about_text1" style={{width: '100%',
    textAlign: 'center',
    justifyContent: 'center'}}>
    <h2 style={{fontSize: '30px',
    fontWeight: '600',
    fontFamily: 'Fredoka One'}}>{head}</h2>
    <p>{text}</p>
                </div>
            <div className="about_image">
            <img src={image} alt="image" style={{height:size}}/>
                </div>
        
                </div>
        </Hidden>
           
                <Hidden only={['xs','sm','md']}>
                <div className="about_header">
    <h1>{title}</h1>
            </div>
            <div className="about_info">
            <div className="about_text">
    <h2>{head}</h2>
    <p>{text}</p>
                </div>
            <div className="about_image">
            <img src={image} alt="image" style={{height:size}}/>
                </div>
        
                </div>
        </Hidden>
            
                
        </div>
    );
}
About.propTypes = {
    width: PropTypes.oneOf(['lg', 'md', 'sm', 'xl', 'xs']).isRequired,
  };
  export default withWidth()(About);
