import React from 'react';
import './Query.css';
function Query({Ques,ans,image}) {
    return (
        <div className="query">
            <div className="query_header">
    <h1>{Ques}</h1>
            </div>
            <div className="query_info">
            <div className="query_text">
    
    <p>{ans}</p>
                </div>
            <div className="query_image">
            <img src={image} alt="image"/>
                </div>
        
                </div>
       
        </div>
    );
}

export default Query;