import React , { useState, useRef } from 'react';
import './Video.css'
import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline';
function Video() {
    const [isVideoPlaying, setisVideoPlaying] = useState(false);
    const videoRef = useRef(null);
    const [dis,setDis] = useState('block'); 
    const onVideoPress =  () => {
    if(isVideoPlaying){
videoRef.current.pause();
setisVideoPlaying(false)
setDis('block');
    }
    else{
        videoRef.current.play();
        setisVideoPlaying(true)
        setDis('none');
    }
}
    return (
        <div className="video" style={{display:'grid',placeItems:'center'}}>

            <video src="https://www.flyploader.live/media/files/video.mp4" ref={videoRef} onClick={onVideoPress} loop /> 
                        <PlayCircleOutlineIcon  onClick={onVideoPress}  style={{color:'white', display:dis, fontSize:'60px', position:'relative',top:'-179px',zIndex:'100'}}/>
        </div>
    );
}

export default Video;