import React from 'react';
import './Help.css';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import SendIcon from '@material-ui/icons/Send';
function Help() {

    const useStyles = makeStyles((theme) => ({
        button: {
          margin: theme.spacing(1),
        },
      }));
      const classes = useStyles();
    return (
        <div className="help">
            <div className="help_head">
                <h1>Need Instant Help ?</h1>
            </div>
            <div className="help_textarea">
<textarea placeholder="Type Your Problem" style={{width:'100%',height:'200px',fontSize:'20px',padding:'20px',border:'1px solid grey' , 
borderRadius:'20px',fontFamily: 'Kufam' }}/>
        </div>
        <br />
        <div className="help_send">
        <Button
        variant="contained"
        size="large" 
      style={{width:'100%'}}
        color="primary"
        className={classes.button}
        endIcon={<SendIcon/>}
      >
        Send
      </Button>
</div>
               </div>
    );
}

export default Help;